$(document).on( "pageinit", "#page-fund-shore", function(event) {

  App.StartLoading();

  $(".numura-warning").loadWarning();

  $.ajax({
      url: Constants.getFundShareUrl(),
      type:"GET"
  }).done(function(response){

    var excludeValueList = _.pluck(
      _.pluck(
        _.filter(response,function(item){
          return item.FundShores.length === 0;
        }),
        'Company'),
      'CompanyId');

    $("#result-block").data("fund-shore-list",response);

    $("#select-company").loadSelectFundCompany({
      "isretail":true,
      "excludeValueList" : excludeValueList,
      "success":function(){
        $("#select-company").trigger('change');
        App.StopLoading();       
      }
    });
    
  });  

});

$(document).on("change","#select-company",function(event){ 
  var fundShoreList = $("#result-block").data("fund-shore-list");
  var filterList = _.filter(fundShoreList,function(item){
    return item.Company.CompanyId+"" === $("#select-company").val()+"";
  });
  var aFundShore = filterList[0];
  var shoreHtml = [];
  shoreHtml.push("<table data-role='table'>");
  shoreHtml.push(" <thead>");
  shoreHtml.push("   <th>配息<br>公布月份</th>");
  shoreHtml.push("   <th>配息<br>基準日</th>");
  shoreHtml.push("   <th>除息日</th>");
  shoreHtml.push(" </thead>");
  $.each(aFundShore.FundShores,function(index,fundShoreItem){
    var NowDate = new Date(),
        ShoreDate = new Date(fundShoreItem.ShoreDate),
        DatumDate = new Date(fundShoreItem.DatumDate),
        Eliminates = new Date(fundShoreItem.Eliminates),
        fmtNowDate = NowDate.format("yyyymm"),
        fmtShoreDate = ShoreDate.format("yyyymm"),
        cxCurrent = (fmtNowDate === fmtShoreDate) ? "current" : "";
    shoreHtml.push("<tbody class='" + cxCurrent + "'>");
    shoreHtml.push("  <td>" + ShoreDate.format("yyyy年mm月") + "</td>");
    shoreHtml.push("  <td>" + DatumDate.format("yyyy/mm/dd") + "</td>");
    shoreHtml.push("  <td>" + Eliminates.format("yyyy/mm/dd") + "</td>");
    shoreHtml.push("</tbody>");
  });         
  shoreHtml.push("</table>");
  $("#result-block").html(shoreHtml.join(" "));      
});

